import React from "react";
import { range } from "util/range";

const KEYS = range(1, 9).map((e) => String(e));
KEYS.push('0');

interface Props {
  submitAllowed: boolean;
  onKeyPress: (key: string) => void;
  onReset: () => void;
  onSubmit: () => void;
}

export default (props: Props) => {
  const { submitAllowed, onKeyPress, onReset, onSubmit } = props;

  return (
    <>
      {KEYS.map((v) => (
        <button key={v} onClick={() =>onKeyPress(v)} type="button">
          {v}
        </button>
      ))}
      <button type="reset" onClick={onReset}>
        Reset
      </button>
      <button disabled={!submitAllowed} onClick={onSubmit}>
        Submit
      </button>
    </>
  );
};
