import { generateRandomInteger } from "util/random";
import { availableOperationsMap } from "./constants";
import { MathExercise, MathSettings } from "./types";

export * from "./constants";
export * from "./helpers";
export * from "./types";

export const generateExercise = (settings: MathSettings): MathExercise => {
  const operationIndex = generateRandomInteger(0, settings.strategy.selected.operations.length - 1);
  const operator = settings.strategy.selected.operations[operationIndex];

  return availableOperationsMap.get(operator)!.generator(settings.strategy.selected);
};
