import React, { useState } from "react";
import { StyledCheckBox } from "components/styled";
import styles from "./settings.module.css";

interface ViewModel {
  availableStrategies: string[];
  selectedStrategy: string;
}

interface Props {
  data: ViewModel;
  visible?: boolean;
  onStrategyChange?: (strategy: string) => void;
}

export default (props: Props) => {
  const { visible } = props;
  const { availableStrategies, selectedStrategy } = props.data;
  const [current, setCurrent] = useState(selectedStrategy);

  const handleChange = (strategy: string) => () => {
    props.onStrategyChange && props.onStrategyChange(strategy);
    setCurrent(strategy);
  };

  return (
    <div className={`${styles.slider} ${visible && styles.open}`} >
      {availableStrategies.map((e) => (
        <StyledCheckBox key={e} id={e} text={e} onChange={handleChange(e)} checked={e === current} />
      ))}
    </div>
  );
};
