import { useState } from "react";
import { defaultSettings, Settings } from "../model/settings";

const getMathStrategy = () => {
  const defaultMathStrategy = defaultSettings.math.strategy.selected;
  const userMathStrategyName = localStorage.getItem("mathStrategy");
  const userMathStrategy = defaultSettings.math.strategy.available.find((e) => e.name === userMathStrategyName);
  return userMathStrategy || defaultMathStrategy;
}

const buildDefaultSettings = () => {
  const result = defaultSettings;
  result.math.strategy.selected = getMathStrategy();
  return result;
};

export const useSettingsContext = () => {
  const [settings, setSettings] = useState(buildDefaultSettings);
  const update = (settings: Settings) => {
    localStorage.setItem("mathStrategy", settings.math.strategy.selected.name);
    setSettings(settings);
  };

  return { value: { settings, update } };
};
