import {
  buildAdditionOperation,
  buildDivisionOperation,
  buildMultiplicationOperation,
  buildSubtractionOperation,
} from "./operations";
import { OperationSign, Strategy } from "./types";

const allOperations = [
  OperationSign.ADDITION,
  OperationSign.SUBTRACTION,
  OperationSign.DIVISION,
  OperationSign.MULTIPLICATION,
];

export const strategies: Strategy[] = [
  { level: 1, name: "easy", operations: [OperationSign.ADDITION, OperationSign.SUBTRACTION] },
  { level: 2, name: "medium", operations: allOperations },
  { level: 3, name: "hard", operations: allOperations },
];

export const availableOperationsMap = new Map([
  [OperationSign.ADDITION, buildAdditionOperation()],
  [OperationSign.SUBTRACTION, buildSubtractionOperation()],
  [OperationSign.MULTIPLICATION, buildMultiplicationOperation()],
  [OperationSign.DIVISION, buildDivisionOperation()],
]);

export const defaultSettings = {
  strategy: {
    available: strategies,
    selected: strategies[1],
  },
};
