import React, { useContext } from "react";
import { SettingsContext } from "../context";
import Settings from "./settings-presenter";

interface Props {
  onClose: () => void;
  visible?: boolean;
}
export default ({ onClose, visible }: Props) => {
  const { settings, update } = useContext(SettingsContext);

  const handleStrategyChange = (strategyName: string) => {
    const newStrategy = settings.math.strategy.available.find(e => e.name === strategyName)
    settings.math.strategy.selected = newStrategy!;
    update && update({ ...settings });
    onClose();
  }

  return (
    <Settings onStrategyChange={handleStrategyChange} data={{
      availableStrategies: settings.math.strategy.available.map(e => e.name),
      selectedStrategy: settings.math.strategy.selected.name
    }} visible={visible}/>
  );
};
