import { availableOperationsMap } from "./constants";
import { MathExercise } from "./types";

const solve = (exercise: MathExercise) => {
  const { leftOperand, rightOperand, operator } = exercise;
  const exerciseOperation = availableOperationsMap.get(operator)!;
  const result = exerciseOperation.logic(leftOperand)(rightOperand);

  return result;
};

export const isCorrect = (exercise: MathExercise) => (userInput: number) => {
  const correctResult = solve(exercise);
  const isCorrect = correctResult === userInput;

  return isCorrect;
};
