import React, { useCallback, useContext, useEffect, useState } from "react";
import { useMathExercise } from "hooks/use-math-exercise";
import { SettingsContext } from "components/context";
import MathPresenter from "./math-presenter";

interface Props {
  onShowSettings: () => void;
}

export default ({onShowSettings}: Props) => {
  const { exercise, next, check } = useMathExercise();
  const [answerValid, setAnswerValid] = useState<boolean>(true);
  const [stats, setStats] = useState({ correct: 0, redo: 0 });
  const { settings } = useContext(SettingsContext);
  const memoNext = useCallback(next, [settings.math])
  useEffect(() => { memoNext(); }, [memoNext, settings])

  const isUserRight = (value: string) => check(Number.parseInt(value));

  const buildNextExercise = () => {
    next();
    setAnswerValid(true);
  };

  const incrementCorrectStats = () => setStats({ ...stats, correct: stats.correct + 1 });
  const incrementRedoStats = () => setStats({ ...stats, redo: stats.redo + 1 });

  const handleSubmit = (userInput: string) => {
    const answerValid = isUserRight(userInput);
    if(answerValid){
      buildNextExercise();
      incrementCorrectStats();
    }else {
      incrementRedoStats();
    }
    setAnswerValid(answerValid);
  };

  return (
    <MathPresenter
      stats={stats}
      leftOperand={exercise.leftOperand}
      rightOperand={exercise.rightOperand}
      operator={exercise.operator}
      error={!answerValid}
      onSubmit={handleSubmit}
      onShowSettings={onShowSettings}
    />
  );
};
