import React, { useState } from "react";
import { useSettingsContext } from "../../hooks/use-settings-context";
import { Background } from "../background";
import { SettingsContext } from "../context";
import { MathExercise } from "../exercise/math";
import {
  SettingsPanel
} from "../settings";
import styles from "./app.module.css";

function App() {
  const [settingsVisible, setSettingsVisible] = useState(false);
  const settingsContext = useSettingsContext();
  
  return (
    <SettingsContext.Provider {...settingsContext}>
      <div className={styles.main}>
        <Background />
        <MathExercise onShowSettings={() => setSettingsVisible(!settingsVisible)} />
        <SettingsPanel onClose={() => setSettingsVisible(!settingsVisible)} visible={settingsVisible} />
      </div>
    </SettingsContext.Provider>
  );
}

export default App;
