export type OperatorType = (a: number, b: number) => number;

export enum OperationSign {
  ADDITION = "+",
  SUBTRACTION = "-",
  MULTIPLICATION = "*",
  DIVISION = ":",
}

export interface MathExercise {
  leftOperand: number;
  rightOperand: number;
  operator: OperationSign;
}

export interface MathSettings {
  strategy: {
    available: Strategy[];
    selected: Strategy;
  };
}

export interface Strategy {
  level: number;
  name: string;
  operations: OperationSign[];
}
