import React, { useEffect, useState } from "react";
import { FaCheck, FaCog, FaRedo } from "react-icons/fa";
import { useInput } from "../../../hooks/use-input";
import { Keypad } from "../../keypad";
import styles from "./math.module.css";

interface Props {
  stats: { correct: number; redo: number };
  error: boolean;
  leftOperand: number;
  rightOperand: number;
  operator: string;
  onSubmit: (value: string) => void;
  onShowSettings: () => void;
}

export default (props: Props) => {
  const { value, inputProps, reset, setValue } = useInput("");
  const {
    onSubmit,
    onShowSettings,
    leftOperand,
    rightOperand,
    operator,
    stats,
  } = props;
  const [error, setError] = useState(props.error);
  useEffect(() => setError(props.error), [props.error]);

  const handleKeypadInput = (newValue: string) => setValue(value + newValue);
  const handleKeypadReset = () => reset();
  const handleKeypadSumit = () => {
    onSubmit(value);
    reset();
    setError(props.error);
    setTimeout(() => {
      setError(false);
    }, 1000);
  };

  return (
    <div className={styles.main}>
      <div className={styles.header}>
        <div className={styles.statsContainer}>
          <div className={styles.stat}>
            <span>{stats.correct}</span>
            <FaCheck />
          </div>
          <div className={styles.stat}>
            <span>{stats.redo}</span>
            <FaRedo />
          </div>
        </div>
        <div>
          <FaCog
            className={styles.settingsButton}
            onClick={onShowSettings}
          />
        </div>
      </div>
      <hr className={styles.rainbowHr} />
      <div className={styles.exercise}>
        <input readOnly value={leftOperand}></input>
        <span>{operator}</span>
        <input readOnly value={rightOperand}></input>
        <span>=</span>
        <input
          className={error ? styles.shake : props.error ? styles.error : ""}
          {...inputProps}
          readOnly
        ></input>
      </div>
      <hr className={styles.rainbowHr} />
      <div className={styles.keypad}>
        <Keypad
          onKeyPress={handleKeypadInput}
          onReset={handleKeypadReset}
          onSubmit={handleKeypadSumit}
          submitAllowed={value !== ""}
        />
      </div>
    </div>
  );
};
