import { useContext, useState } from "react";
import { SettingsContext } from "../components/context";
import { generateExercise, isCorrect, MathExercise } from "../model/math";

export const useMathExercise = () => {
  const { settings } = useContext(SettingsContext);
  const [exercise, setExercise] = useState<MathExercise>(
    generateExercise(settings.math)
  );
  const next = () => setExercise(generateExercise(settings.math));
  const check = (userInput: number): boolean => isCorrect(exercise)(userInput);
  return { exercise, next, check };
};
