import { generateRandomInteger } from "util/random";
import { OperationSign, OperatorType, Strategy } from "./types";

const operation = (operator: OperatorType) => (left: number) => (right: number) => operator(left, right);

/**
 * Addition
 */
export const buildAdditionOperation = () => {
  const logic = operation((a, b) => a + b);

  const generator = (strategy: Strategy) => {
    const max = Math.pow(10, strategy.level);

    const leftOperand = generateRandomInteger(0, max);
    const rightOperand = generateRandomInteger(0, max - leftOperand);

    return { operator: OperationSign.ADDITION, leftOperand, rightOperand };
  };
  return { logic, generator };
};

/**
 * Subtraction
 */
export const buildSubtractionOperation = () => {
  const logic = operation((a, b) => a - b);

  const generator = (strategy: Strategy) => {
    const max = Math.pow(10, strategy.level);

    const leftOperand = generateRandomInteger(0, max);

    const rightOperand = generateRandomInteger(0, max - leftOperand);

    return {
      operator: OperationSign.SUBTRACTION,
      leftOperand: leftOperand > rightOperand ? leftOperand : rightOperand,
      rightOperand: leftOperand > rightOperand ? rightOperand : leftOperand,
    };
  };

  return { logic, generator };
};

/**
 * Multiplication
 */

export const buildMultiplicationOperation = () => {
  const logic = operation((a, b) => a * b);

  const generator = (strategy: Strategy) => {
    const max = 5 * strategy.level;

    const leftOperand = generateRandomInteger(0, max);
    const rightOperand = generateRandomInteger(0, max);

    return { operator: OperationSign.MULTIPLICATION, leftOperand, rightOperand };
  };

  return { logic, generator };
};

/**
 * Division
 */

export const buildDivisionOperation = () => {
  const logic = operation((a, b) => (b !== 0 ? a / b : NaN));

  const generator = (strategy: Strategy) => {
    const max = 5 * strategy.level;

    const leftOperand = generateRandomInteger(0, max);

    const rightOperand = generateRandomInteger(0, max);

    return {
      operator: OperationSign.DIVISION,
      leftOperand: leftOperand * rightOperand,
      rightOperand: rightOperand > 0 ? rightOperand : 1,
    };
  };
  return { logic, generator };
};
